.listing-section {
    padding-top: 32px;
    margin-bottom: clamp(80px, 10.417vw, 150px);

    &__wrapp {
        position: relative;
        display: grid;
        gap: clamp(24px, 2.778vw, 40px);

        @include for-tablet {
            grid-template-columns: 29% 71%;
            align-items: flex-start;
        }

        @include tablet {
            grid-template-columns: 100%;
        }
    }

    &__aside {
        padding: 16px 16px 40px;
        background-color: var(--cl-k);
        border-radius: 16px;
        display: flex;
        flex-direction: column;
        gap: 16px;

        @include for-tablet {
            position: sticky;
            top: var(--header-top);
        }
    }

    &__categories {
        display: flex;
        flex-direction: column;
        gap: 12px;

        a {
            padding: 12px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 8px;

            svg {
                stroke: var(--cl-c);
                flex-shrink: 0;
            }

            span {
                display: flex;
                align-items: center;
                gap: 8px;
                font-size: 16px;
                line-height: normal;
                font-weight: 500;
                color: var(--cl-c);
            }
        }
    }

    &__subсategories {
        display: flex;
        flex-direction: column;
        gap: 12px;

        a {
            padding: 12px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 8px;

            svg {
                stroke: var(--cl-c);
                flex-shrink: 0;
            }

            span {
                display: flex;
                align-items: center;
                gap: 8px;
                font-size: 16px;
                line-height: normal;
                font-weight: 500;
                color: var(--cl-c);
            }
        }
    }

    &__category-active {
        padding: 12px;
        background-color: var(--cl-a);
        border-radius: 8px;

        display: flex;
        align-items: center;
        justify-content: space-between;

        svg {
            stroke: var(--cl-c);
            flex-shrink: 0;
        }

        span {
            display: flex;
            align-items: center;
            gap: 8px;
            font-size: 16px;
            line-height: normal;
            font-weight: 500;
            color:  var(--cl-c);
        }
    }

    &__box {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: clamp(32px, 2.778vw, 40px);
    }

    &__details {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 24px;
    }

    &__top {
        width: 100%;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        gap: 24px 16px;

        @include small-tablet {
            flex-direction: column;
        }
    }

    &__summary {
        display: flex;
        flex-direction: column;
        gap: 8px;

        &-list {
            display: flex;
            align-items: center;
            gap: 10px 21px;

            @include desktop {
                flex-direction: column;
                align-items: flex-start;
            }

            li {
                position: relative;
                font-size: 24px;
                line-height: 140%;
                font-weight: 700;
                color: var(--cl-c);

                &::before {
                    content: '';
                    position: absolute;
                    left: -13px;
                    top: 50%;
                    transform: translateY(-50%);
                    width: 5px;
                    height: 5px;
                    background-color: var(--cl-q);
                    border-radius: 50%;
                }

                &:first-child::before {
                    display: none;
                }
            }
        }

        &-posts {
            font-size: 16px;
            line-height: 160%;
            font-weight: 500;
            color: var(--cl-r);
        }
    }

    &__tags {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 16px;

        &-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 16px;
        }

        &-heading {
            font-size: 16px;
            line-height: 150%;
            font-weight: 500;
            color: var(--cl-j);
        }

        &-controls {
            display: flex;
            align-items: center;
            gap: 40px;
        }

        &-items {
            width: 100%;
        }

        .swiper-slide {
            width: fit-content;
        }

        .tag {
            padding: 10px 16px;
            font-size: 14px;
            line-height: 150%;
        }
    }

    &__items {
        width: 100%;
        display: grid;
        gap: 40px 16px;

        @include for-small-tablet {
            grid-template-columns: repeat(2, 1fr);
        }

        @include small-tablet {
            grid-template-columns: 100%;
        }
    }
}

body[data-theme="dark"] {
    .listing-section {
        &__aside {
            background-color: var(--cl-h);
        }

        &__categories {
            a {
                svg {
                    stroke: var(--cl-d);
                }
                
                span {
                    color: var(--cl-b);

                    svg {
                        stroke: var(--cl-b);
                    }
                }
            }
        }

        &__subсategories {
            a {
                svg {
                    stroke: var(--cl-d);
                }
                
                span {
                    color: var(--cl-b);

                    svg {
                        stroke: var(--cl-b);
                    }
                }
            }
        }

        &__category-active {
            background-color: var(--cl-f);

            svg {
                stroke: var(--cl-b);
            }

            span {
                color: var(--cl-b);
            }
        }

        &__summary {
            &-list li {
                color: var(--cl-b);
            }

            &-posts {
                color: var(--cl-e);
            }
        }

        &__tags-heading {
            color: var(--cl-p);
        }
    }
}